* {
  margin: 0;
  padding: 0;
}
body {
  background: url(https://i.pinimg.com/originals/d6/70/99/d670990fa86f31233a53a22d7bb2f4bc.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.header {
  color: #a27038;
  text-align: center;
  font-size: 40px;
}
.app {
  background: linear-gradient(5deg, #886a47 0%, #317ec6 100%);
  width: 95%;
  min-height: 200px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}
.Question-section {
  width: 100%;
  position: relative;
}
.Question-count {
  margin-bottom: 20px;
}
.Question-text {
  margin-bottom: 12px;
  color: rgb(160, 173, 120);
  font-size: 30px;
  font-weight: bold;
}
.Answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
button {
  width: 80%;
  font-size: 17px;
  font-weight: 600;
  text-align: center;
  color: #fff;
  background: linear-gradient(5deg, #886a47 0%, #317ec6 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: center;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
  transition: transform 0.3s ease; 
}
button:hover {
  color: #0d3357;
  transform: scale(1.1);
}
button:focus {
  outline: none;
}
.score-card {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(160, 173, 120);
  font-size: 30px;
  font-weight: bold;
}
